<template>
<div>
  <!-- 头部 -->
  <div class="header">
    <div class="title">加班登记</div>
  </div>
  <!-- 侧边栏 -->
  <div class="aside">
    <img class="img-aside" src="@/assets/images/插图.png">
    <img class="img-title" src="@/assets/images/logo.png"/>
    <div class="icon">
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-edit-outline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;加班登记</i>
    </div>
    <router-link to="/settingmain">
      <div class="icon2">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-setting">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;系统设置</i>
      </div>
    </router-link>
    <div class="user">
      <img  class="user-img" src="@/assets/images/user-img.jpg">
      <p class="user-name">{{this.name}}</p>
    </div>
    <el-button class="exit" @click="handleExit()">退出登录</el-button>
  </div>

  <!-- 主页面 -->
  <div class="main">
    <div class="content">
      <div class="main-content">
        <el-tabs v-model="activeName">
          <!-- 加班明细页面 -->
          <el-tab-pane label="加班明细" name="first">
            <el-col>
              <el-form :inline="true">
                <el-form-item style="float: left" label="查询人员信息:">
                  <el-input v-model="keyUser" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-date-picker  width="20" type="month" placeholder="选择登记时间"  v-model="month">
                  </el-date-picker>
                </el-form-item>
                <el-form-item style="float: right">
                  <el-button type="success" icon="el-icon-download" @click="handleDownloadingdetail()" >导出</el-button>
                </el-form-item >
                <el-form-item style="float: right">
                  <el-button type="primary" icon="el-icon-edit-outline" @click="handleAdd()" >新增</el-button>
                </el-form-item >
              </el-form>
            </el-col>
            <div class="main-table">
              <el-table :data="searchUser(month,keyUser)" border height="370" style="width: 100%" >
                <el-table-column type="selection" width="30" align="center"></el-table-column>
                <el-table-column  fixed type="index" label="序号" width="50" align="center"></el-table-column>
                <el-table-column fixed prop="name" label="姓名" width="80" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.name}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="uid" label="工号" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.uid}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="createdate" label="日期" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.createdate | dateformat('YYYY-MM-DD')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="project" label="项目" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.project}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="overtimereason" label="加班事由" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.overtimereason}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="startdate" label="开始时间" width="105" align="center">\
                  <template slot-scope="scope">
                    <span>{{scope.row.startdate | dateformat('HH:mm')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="enddate" label="结束时间" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.enddate | dateformat('HH:mm')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="status" label="加班费/补休" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.status}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="timecount" label="加班时长" width="105" align="center"></el-table-column>
                <el-table-column prop="dayrest" label="平时补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="weekendrest" label="周末补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="holidayrest" label="节假日补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="daycharge" label="平时加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="weekendcharge" label="周末加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="holidaycharge" label="节假日加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="mealpay" label="餐补" width="105" align="center"></el-table-column>
                <el-table-column prop="trafficpay" label="交通补贴" width="105" align="center"></el-table-column>
                <el-table-column prop="overtimeposition" label="加班地点" width="105" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.overtimeposition}}</span>
                  </template>
                </el-table-column>
                <el-table-column fixed="right" label="操作" align="center" width="150">
                  <template slot-scope="scope">
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button size="mini" @click="handleEdit(scope.$index,scope.row)">编辑</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <AddUser :dialogAdd="dialogAdd" @update="getUserInfo"></AddUser>
            <EditUser :dialogEdit="dialogEdit" :formeditData="formeditData" @update="getUserInfo"></EditUser>
          </el-tab-pane>
          <!-- 加班汇总页面 -->
          <el-tab-pane label="加班汇总" name="second">
            <el-col>
              <el-form :inline="true">
                <el-form-item style="float: left" label="查询人员信息:">
                  <el-input v-model="keyUsers" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-date-picker  width="20" type="month" placeholder="选择登记时间"  v-model="summonth">
                  </el-date-picker>
                </el-form-item>
                <el-form-item style="float: right">
                  <el-button type="success" icon="el-icon-download" @click="handleDownloadingsum()" >导出</el-button>
                </el-form-item >
              </el-form>
            </el-col>
            <div class="main-table">
              <el-table :data="searchUsersum(keyUsers)" border height="370" style="width: 100%" >
                <el-table-column fixed prop="name" label="姓名" width="80" align="center"></el-table-column>
                <el-table-column prop="uid" label="工号" width="105" align="center"></el-table-column>
                <el-table-column prop="month" label="月份" width="105" align="center"></el-table-column>
                <el-table-column prop="workdayB" label="平时补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="weekendB" label="周末补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="holidayB" label="节假日补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="workdayJ" label="平时加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="weekendJ" label="周末加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="holidayJ" label="节假日加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="allTime" label="加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="mealpay" label="餐补" width="105" align="center"></el-table-column>
                <el-table-column prop="allCarpay" label="交通补贴" width="105" align="center"></el-table-column>
                <el-table-column prop="paysum" label="合计" width="80" align="center"></el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</div>
</template>
<script>
  import AddUser from './components/Add'
  import EditUser from './components/Edit'
  import axios from 'axios'
  export default {
    name: 'AdminMain',
    components: {
      AddUser,
      EditUser
    },
    data () {
      return {
        msgData: [],
        sumData: [],
        name: localStorage.getItem('name'),
        uid: localStorage.getItem('uid'),
        dialogAdd: {     //是否显示新增信息
          show: false
        },
        dialogEdit: {   //是否显示编辑信息
          show: false
        },
        month:'',
        summonth: new Date(),    //搜索创建月份
        activeName: 'first',
        keyUser: '',  //搜索姓名
        keyUsers: '',
        formeditData: {
          uid: '',
          project: '',
          createdate: '',
          startdate: '',
          enddate: '',
          status: '',
          overtimereason: '',
          overtimeposition: ''
        },
      }
    },
    methods:{
      handleAdd () {    //显示新增信息
				this.dialogAdd.show = true
      },
      handleExit() {
        this.$router.replace('/')
      },
      handleEdit (index,row) {   //显示编辑信息
        this.dialogEdit.show = true
        this.formeditData = {
          uid: row.uid,
          project: row.project,
          createdate: row.createdate,
          startdate: row.startdate,
          enddate: row.enddate,
          status: row.status,
          overtimereason: row.overtimereason,
          overtimeposition: row.overtimeposition,
          id: row.id
        }
      },
      handleDelete(index,row) {  // 删除加班记录
        // console.log(row.id)
        this.$confirm('此操作将删除此条加班记录，是否继续？','警告',{
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          // console.log(row.id)
          this.$axios({
            url: '/api/overtime/delete',     //根据id删除对应的加班记录
            method: 'delete',
            headers: {
              'Authorization': 'Bearer '+localStorage.getItem('token')
            },
            data: {
              'id': parseInt(row.id)
            }
          }).then(res =>{
            this.$message({
              type:"success",
              message:"删除信息成功"
            })
            this.getUserInfo()    //删除数据，更新视图
          })
        }).catch(()=>{})
      },
      //导出加班明细表，包括姓名，工号，项目，日期，开始时间，结束时间，加班费/补休，加班事由，加班地点
      handleDownloadingdetail() {
        this.$confirm('此操作将导出excel文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const detailData=[]
          if(this.msgData){
            this.msgData.forEach(item =>{
              const Y1 = new Date(this.month).getFullYear()
              const M1 = new Date(this.month).getMonth() + 1
              const Y2 = new Date(item.createdate).getFullYear()
              const M2  = new Date(item.createdate).getMonth() + 1
              // item.createdate = dateformat(new Date(item.createdate),'YYYY-MM-DD')
              // console.log(item.createdate)
              // item.startdate = dateformat(new Date(item.startdate),'HH:mm')
              // item.enddate = dateformat(new Date(item.enddate),'HH:mm')
              if(Y1==Y2 && M1==M2){
                detailData.push(item)
              }
            })
          }
          this.excelData = detailData  //导出的加班明细表
          require.ensure([], () => {
            const { export_json_to_excel } = require('../../excel/Export2Excel') //必须使用绝对路径
            const tHeader = ['姓名','工号','项目','加班事由','日期','开始时间','结束时间','加班费/补休','','平时加班时数',
              '周末加班时数','节假日加班时数','平时补休时数','周末加班时数','节假日加班时数','餐贴','交通补贴','加班地点']
            const filterVal = ['name','uid','project','overtimereason','createdate','startdate','enddate','status','timecount','daycharge',
              'weekendcharge','holidaycharge','dayrest','weekendrest','holidayrest','mealpay','trafficpay','overtimeposition'] // 导出的表头字段名
            const list = this.excelData
            const data = this.formatJson(filterVal, list)
            export_json_to_excel(tHeader, data,'加班登记表')// 导出的表格名称，根据需要自己命名
          })
        }).catch(() => {})
      },
      handleDownloadingsum() {
        this.$confirm('此操作将导出excel文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.excelData = this.sumData  //导出的加班明细表
          require.ensure([], () => {
            const { export_json_to_excel } = require('../../excel/Export2Excel') //必须使用绝对路径
            const tHeader = ['姓名','工号','平时加班小时数','周末加班小时数','节假日加班小时数',
              '平时补休小时数','周末补休小时数','节假日补休小时数','总加班时长','餐补','交通补贴','合计'] // 导出的表头名
            const filterVal = ['name','uid','workdayJ','weekendJ','holidayJ','workdayB','weekendB',
              'holidayB','allTime','mealpay','allCarpay','paysum'] // 导出的表头字段名
            const list = this.excelData
            const data = this.formatJson(filterVal, list)
            export_json_to_excel(tHeader, data,'加班汇总表')// 导出的表格名称，根据需要自己命名
          })
        }).catch(() => {})
      },
      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
      },
      getUserInfo () {
        this.$axios({     //获取所有的加班记录
          url: '/api/overtime/getAllOvertimeRecord',
          method: 'get',
          headers: {
            'Authorization': 'Bearer '+localStorage.getItem('token')
          }
        }).then(res =>{
          const msg = res.data
          this.$axios({     //获取所有的用户信息
            url: '/api/users/all',
            method: 'get',
            headers: {
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res =>{
            // console.log(res)
            this.msgData = []
            const users = res.data.data.userInfos
            if(msg){     //取出用户表中的姓名放在加班信息表中
              msg.forEach(item =>{
                // console.log(item)
                if(users){
                  users.forEach(i =>{
                    if(parseInt(i.username) == item.uid){
                      var user = {
                        id: '',
                        name: '',
                        uid: '',
                        project: '',
                        createdate: '',
                        overtimeposition: '',
                        overtimereason: '',
                        startdate: '',
                        enddate: '',
                        status: '',
                        dayrest: '',
                        weekendrest: '',
                        holidayrest: '',
                        daycharge: '',
                        weekendcharge: '',
                        holidaycharge: '',
                        timecount: '',
                        mealpay: '',
                        trafficpay: ''
                      }
                      user.id = item.id
                      user.name = i.name
                      user.uid = item.uid
                      user.project = item.project
                      user.createdate = item.startdate
                      user.startdate = item.startdate
                      user.enddate = item.enddate
                      user.overtimeposition = item.overtimeposition
                      user.overtimereason = item.overtimeposition
                      user.status = item.status
                      user.timecount = item.timecount
                      user.mealpay = item.pay
                      user.trafficpay = item.carpay
                      if(item.datetypevalue == 1){
                        if(item.status == "加班费")
                          user.daycharge = item.timecount
                        else if(item.status == "补休")
                          user.dayrest = item.timecount
                      }
                      else if(item.datetypevalue == 2){
                        if(item.status == "加班费")
                          user.weekendcharge = item.timecount
                        else if(item.status == "补休")
                          user.weekendrest = item.timecount
                      }
                      else if(item.datetypevalue == 3){
                        if(item.status == "加班费")
                          user.holidaycharge = item.timecount
                        else if(item.status == "补休")
                          user.holidayrest = item.timecount
                      }
                      // console.log(item)
                      this.msgData.push(user)
                    }

                  })
                }
              })
            }
          })
        })
        this.getUsersum()
      },
      getUsersum(){
        this.sumData = []
        this.$axios({
          url: '/api/users/all',
          method: 'get',
          headers: {
            'Authorization': 'Bearer '+localStorage.getItem('token')
          }
        }).then(res =>{
          const users = res.data.data.userInfos
          if(users){
            users.forEach(item =>{
              const Y = new Date(this.summonth).getFullYear()
              const M = new Date(this.summonth).getMonth()+1
              const month = Y*100+M
              this.$axios({
                url: '/api/overtime/Summary',
                method: 'post',
                headers: {
                  'Authorization': 'Bearer '+localStorage.getItem('token')
                },
                data:{
                  'uid': parseInt(item.username),
                  'month': parseInt(month)
                }
              }).then(res =>{
                const data = res.data
                // console.log(res.data)
                var sum = {
                  name: '',
                  month: '',
                  workdayB: '',
                  workdayJ: '',
                  weekendB: '',
                  weekendJ: '',
                  holidayB: '',
                  holidayJ: '',
                  allTime: '',
                  allCarpay: '',
                  mealpay: '',
                  uid: '',
                  paysum: ''
                }
                sum.uid = item.username
                sum.name = item.name
                sum.month = data.month
                sum.workdayB = data.workdayB
                sum.workdayJ = data.workdayJ
                sum.weekendB = data.weekendB
                sum.weekendJ = data.weekendJ
                sum.holidayB = data.holidayB
                sum.holidayJ = data.holidayJ
                sum.allTime = data.allTime
                sum.allCarpay = data.allCarpay
                sum.mealpay = data.allPay
                sum.paysum = data.allCarpay+data.allPay
                this.sumData.push(sum)
              })
            })
          }
        })
      },
      searchUser(month,keyUser){
        return this.msgData.filter(item =>{
          if(!month && !keyUser){
            return item
          }
          else if(month && !keyUser){
            const Y1 = new Date(this.month).getFullYear()
            const M1 = new Date(this.month).getMonth() + 1
            const Y2 = new Date(item.createdate).getFullYear()
            const M2  = new Date(item.createdate).getMonth() + 1
            if(Y1==Y2 && M1==M2){
              return item
            }
          }
          else if(!month && keyUser){
            if(item.name.includes(keyUser)){
              return item
            }
          }
          else if(month && keyUser){
            const Y1 = new Date(this.month).getFullYear()
            const M1 = new Date(this.month).getMonth() + 1
            const Y2 = new Date(item.createdate).getFullYear()
            const M2  = new Date(item.createdate).getMonth() + 1
            if(Y1==Y2 && M1==M2){
              if(item.name.includes(keyUser)){
                return item
              }
            }
          }
        })
      },
      searchUsersum(keyUsers) {
        return this.sumData.filter(item =>{
          if(!keyUsers){
            return item
          }
          else if(keyUsers){
            if(item.name.includes(keyUsers)){
              return item
            }
          }
        })
      }
    },
    mounted () {
      this.getUserInfo(),
      this.getUsersum()
    }
  }
</script>
<style lang="stylus" scoped>
  .header
    position: absolute
    top: 0
    right: 0
    left: 200px
    height: 56px
    background: #fff
    .title
      margin-left: 28px
      line-height: 56px
      font-size: 16px
      color: rgba(0,0,0,0.85)
  .aside
    position:absolute
    top: 0
    left: 0
    bottom: 0
    width: 200px
    background: #1B202E
    .img-aside
      position: absolute
      bottom: 0
      width: 200px
    .img-title
      position: absolute
      top: 20px
      left: 27px
    .icon
      position: absolute
      top: 75px
      height: 42px
      width: 100%
      text-align: left
      font-size: 15px
      line-height: 42px
      color: #FFFFFF
      background-image: linear-gradient(90deg, #59E3FF 0%, #1F9CFF 100%)
    .icon2
      position: absolute
      top: 117px
      height: 42px
      width: 100%
      text-align: left
      font-size: 15px
      color: #FFFFFF
      line-height: 42px
    .user
      position: absolute
      bottom: 200px
      left: 70px
      right: 70px
      .user-img
        position: absolute
        width: 60px
        height: 60px
        border-radius: 100px
        width: 100%
      .user-name
        position: absolute
        top: 60px
        left: -10px
        width: 80px
        height: 15px
        font-size: 15px
        line-height: 15px
        text-align: center
        color: #fff
    .exit
      position: absolute
      bottom: 50px
      left: 51px
      opacity: 0.4
      color: #fff
      background-color: transparent
  .main
    position: absolute
    top: 56px
    left: 200px
    bottom: 0
    right: 0
    background: #eee
    .content
      position: absolute
      top: 24px
      left: 24px
      bottom: 24px
      right: 24px
      background: #fff
      .main-content
        margin: 8px 20px
        .detail
          margin: 10px
          .main-table
            text-align: center
</style>



